import React, {Component} from 'react'
import Cookies from 'js-cookie';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';


class RecipeList extends Component{
    constructor (props){
          super(props);
          this.state = {
            isLoggedIn: !!Cookies.get('Authorization'),
            data: [],
          }

this.expandRecipe = this.expandRecipe.bind(this);
}

expandRecipe(){
  alert("You triggered a function that does nothing. Well, it sends up this alert, but that's it.")
}


componentDidMount(){
    fetch("/recipes/")
      .then(response => response.json())
      .then(response => this.setState({data: response}));
            }



render() {



const recipeList = this.state.data.map((data) => (
  <div onClick={this.expandRecipe} className="card col-2" key={data.id}>
  <h3>{data.title}</h3>
  <p>{data.type}</p>
  <p>Prep time: {data.preptime}min.</p>
  <p>Cook time: {data.cooktime}min.</p>
  <p>Cook temp: {data.cooktemp}min.</p>
  <p>{`Makes ${data.amount} ${data.units}`}</p>
  <p>Ingredients: {data.ingredientList}</p>
  <p>Instructions: {data.body}</p>
  <p>Notes: {data.notes}</p>
  </div>
))

console.log(this.state.data)
  return (
    <div className="container">
    <div className="row">
      {recipeList}
      </div>
    </div>
  );
}
}

export default RecipeList;
